
@media (max-width: 600px) {
.hide-width {
    display: none !important;
}
}
@media (min-width: 601px) and (max-width: 992px) {
.hide-tablet {
    display: none !important;
}
}
@media (min-width: 993px) {
.hide-desktop {
    display: none !important;
}
}
.content-item {
  margin-left: auto;
  margin-right: auto;
}
.content-item .complete .top-box {
  *zoom: 1;
}
.content-item .complete .top-box:before, .content-item .complete .top-box:after {
    content: "";
    display: table;
}
.content-item .complete .top-box:after {
    clear: both;
}
.content-item {
  width: 100%;
  padding: .32rem;
  margin-top: .12rem;
  background: #fff;
  box-sizing: border-box;
}
.content-item .complete .top-box {
    position: relative;
}
.content-item .complete .top-box .avatar {
      width: 1rem;
      height: 1rem;
      border-radius: 100%;
      overflow: hidden;
      background: #EFF2F7;
      float: left;
      display: inline;
}
.content-item .complete .top-box .avatar img {
        width: 1rem;
        height: 1rem;
}
.content-item .complete .top-box .info {
      width: 65%;
      margin-left: .24rem;
      float: left;
      display: inline;
}
.content-item .complete .top-box .info .title {
        font-size: .28rem;
        line-height: .46rem;
        -webkit-margin-before: .5em;
        color: #1F2D3D;
}
.content-item .complete .top-box .info .tab {
        position: absolute;
        right: 0;
        top: .18rem;
        padding: .01rem .08rem;
        background: #D3DCE6;
        border-radius: 4px;
        font-size: .24rem;
        color: #fff;
}
.content-item .complete .bottom-box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-top: .24rem;
    color: #92a8c1;
}
.content-item .complete .bottom-box .left {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.content-item .complete .bottom-box .left .username {
        padding: 0 !important;
        -webkit-margin-before: 0em;
        -webkit-margin-after: 0em;
}
.content-item .complete .bottom-box .left .time {
        margin-left: .24rem;
        font-size: .24rem;
}
.content-item .complete .bottom-box .right {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.content-item .complete .bottom-box .right .item {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
}
.content-item .complete .bottom-box .right .item:first-child {
          margin-right: .16rem;
}
.content-item .complete .bottom-box .right .item .text {
          font-size: .24rem;
          margin-left: .08rem;
}
.content-item .simple .avatar {
    width: .88rem;
    height: .88rem;
    background: #EFF2F7;
    border-radius: 100%;
    overflow: hidden;
}
.content-item .simple .avatar img {
      width: .88rem;
      height: .88rem;
}
.content-item .simple .info {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    margin-left: .24rem;
}
.content-item .simple .info .title {
      font-size: .28rem;
}
.content-item .simple .info .userinfo {
      margin-top: .08rem;
}
.content-item .simple .info .userinfo .username,
      .content-item .simple .info .userinfo .time {
        font-size: .2rem;
        color: #92a8c1;
}
.content-item .simple .info .userinfo .time {
        margin-left: .12rem;
}



