
@media (max-width: 600px) {
.hide-width {
    display: none !important;
}
}
@media (min-width: 601px) and (max-width: 992px) {
.hide-tablet {
    display: none !important;
}
}
@media (min-width: 993px) {
.hide-desktop {
    display: none !important;
}
}
.mu-bottom-nav-shift {
  background: #1F2D3D !important;
}
.mu-bottom-nav-shift .mu-buttom-item .mu-buttom-item-wrapper .message-count {
    position: absolute;
    top: 6px;
    right: 32px;
    width: .32rem;
    height: .32rem;
    line-height: .32rem;
    text-align: center;
    border-radius: 100%;
    background: #f44336;
    font-size: .2rem;
    color: #fff;
}



