
@media (max-width: 600px) {
.hide-width {
    display: none !important;
}
}
@media (min-width: 601px) and (max-width: 992px) {
.hide-tablet {
    display: none !important;
}
}
@media (min-width: 993px) {
.hide-desktop {
    display: none !important;
}
}
.editPage .edit, .editPage .button-group {
  margin-left: auto;
  margin-right: auto;
}
.editPage {
  position: fixed;
  width: 100%;
  height: 100%;
  background: #fff;
  z-index: 1100;
}
.editPage .mu-tabs {
    background: #41b883;
}
.editPage .mu-tabs .line {
      background: #fff;
}
.editPage .edit {
    width: 88% !important;
    margin-top: .32rem;
}
.editPage .edit .label-focus {
      color: #41b883;
}
.editPage .edit .underline-focus {
      background: #41b883;
}
.editPage .preview {
    width: 100%;
    padding: .32rem;
    margin-top: 0 !important;
    box-sizing: border-box;
    background: #EFF2F7;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
}
.editPage .button-group {
    width: 88%;
    margin: .32rem 0;
}



