
@media (max-width: 600px) {
.hide-width {
    display: none !important;
}
}
@media (min-width: 601px) and (max-width: 992px) {
.hide-tablet {
    display: none !important;
}
}
@media (min-width: 993px) {
.hide-desktop {
    display: none !important;
}
}
.topics {
  position: relative;
  background: #EFF2F7;
}
.topics .mu-circle {
    border-top-color: #41b883 !important;
    border-right-color: #41b883 !important;
    border-left-color: #41b883 !important;
}
.topics .tabs-wrapper {
    width: 100%;
    height: 48px;
}
.topics .tabs-wrapper .mu-tabs {
      position: fixed;
      top: 56px;
      background: #fff !important;
}
.topics .tabs-wrapper .mu-tabs .mu-tab-link {
        color: rgba(31, 45, 61, 0.7);
}
.topics .tabs-wrapper .mu-tabs .mu-tab-active {
        font-size: .3rem;
        color: #1f2d3d;
}
.topics .tabs-wrapper .active-line {
      background: #41b883 !important;
}
.topics .mu-infinite-scroll {
    padding: .24rem 0;
}
.topics .mu-infinite-scroll .mu-infinite-scroll-text {
      font-size: .28rem;
}



