
@media (max-width: 600px) {
.hide-width {
    display: none !important;
}
}
@media (min-width: 601px) and (max-width: 992px) {
.hide-tablet {
    display: none !important;
}
}
@media (min-width: 993px) {
.hide-desktop {
    display: none !important;
}
}
.user .login-wrapper .login {
  margin-left: auto;
  margin-right: auto;
}
.user {
  background: #EFF2F7;
}
.user .mu-text-field.focus-state {
    color: #41b883 !important;
}
.user .mu-text-field .line-focus {
    background: #41b883 !important;
}
.user .mu-raised-button {
    margin-top: .24rem;
}
.user .login-wrapper .login {
    width: 70%;
    margin-top: .54rem;
}
.user .login-wrapper .tip {
    width: 92%;
    margin-top: .64rem;
}
.user .login-wrapper .tip .question p {
      font-weight: bold;
      -webkit-margin-before: 0em;
      -webkit-margin-after: 0em;
      margin-top: .02rem;
      margin-left: .08rem;
}
.user .login-wrapper .tip .content {
      line-height: .48rem;
      padding: .24rem .4rem;
      box-sizing: border-box;
}
.user .userinfo-wrapper .userinfo {
    width: 100%;
    height: 1.35rem;
    margin-top: .32rem;
    padding: 0 .24rem;
    box-sizing: border-box;
    background: #fff;
    border-top: 1px solid #E5E9F2;
    border-bottom: 1px solid #E5E9F2;
}
.user .userinfo-wrapper .userinfo .avatar {
      width: .96rem;
      height: .96rem;
      overflow: hidden;
      border-radius: 4px;
      background: #EFF2F7;
}
.user .userinfo-wrapper .userinfo .avatar img {
        width: .96rem;
        height: .96rem;
}
.user .userinfo-wrapper .userinfo .info {
      position: relative;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      margin-left: .24rem;
}
.user .userinfo-wrapper .userinfo .info .username {
        font-size: .28rem;
        font-weight: bold;
        color: #1F2D3D;
}
.user .userinfo-wrapper .userinfo .info .userid {
        font-size: .22;
        margin-top: .08rem;
}
.user .userinfo-wrapper .userinfo .info .score {
        position: absolute;
        right: 0;
        top: 0;
        width: 1.2rem;
        height: .42rem;
        border-radius: 5rem;
        background: #EFF2F7;
}
.user .userinfo-wrapper .action-wrapper {
    margin-top: .32rem;
    background: #fff;
    border-top: 1px solid #E5E9F2;
    border-bottom: 1px solid #E5E9F2;
}
.user .userinfo-wrapper .action-wrapper .action-item {
      position: relative;
      height: .88rem;
      padding-left: .24rem;
      box-sizing: border-box;
}
.user .userinfo-wrapper .action-wrapper .action-item .title {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        height: 100%;
        margin-left: .16rem;
        border-bottom: 1px solid #E5E9F2;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        font-size: .28rem;
}
.user .userinfo-wrapper .action-wrapper .action-item .count {
        position: absolute;
        top: 50%;
        right: 24px;
        -webkit-transform: translate3d(0, -50%, 0);
                transform: translate3d(0, -50%, 0);
        width: .4rem;
        height: .4rem;
        line-height: .4rem;
        text-align: center;
        font-size: .2rem;
        color: #324057;
        border-radius: 100%;
        background: #E5E9F2;
}
.user .userinfo-wrapper .action-wrapper .action-item:last-child .title {
        border-bottom: none;
}
.user .userinfo-wrapper .btn-logout {
    width: 100%;
    height: .88rem;
    background: #fff;
    margin-top: .32rem;
    border-top: 1px solid #E5E9F2;
    border-bottom: 1px solid #E5E9F2;
    font-size: .28rem;
}
.user .details {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1100;
    width: 100%;
    height: 100%;
    background: #EFF2F7;
}



