
@media (max-width: 600px) {
.hide-width {
    display: none !important;
}
}
@media (min-width: 601px) and (max-width: 992px) {
.hide-tablet {
    display: none !important;
}
}
@media (min-width: 993px) {
.hide-desktop {
    display: none !important;
}
}
.releasePage .textblock {
  margin-left: auto;
  margin-right: auto;
}
.releasePage {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1100;
  width: 100%;
  height: 100%;
  background: #fff;
}
.releasePage .mu-appbar {
    background: #41b883 !important;
}
.releasePage .textblock {
    width: 88%;
    margin-top: .32rem;
}
.releasePage .textblock .label-focus {
      color: #41b883;
}
.releasePage .textblock .underline-focus {
      background: #41b883;
}
.releasePage .textblock .edit-button {
      width: 100%;
      height: .88rem;
      margin-top: .24rem;
      line-height: .88rem;
      border-radius: 4px;
      color: #475669;
      background: #EFF2F7;
}
.releasePage .button-release {
    margin-top: .54rem;
}
.mu-menu-item-wrapper.active {
  color: #41b883 !important;
}



