
@media (max-width: 600px) {
.hide-width {
    display: none !important;
}
}
@media (min-width: 601px) and (max-width: 992px) {
.hide-tablet {
    display: none !important;
}
}
@media (min-width: 993px) {
.hide-desktop {
    display: none !important;
}
}
.no-data {
  position: relative;
  width: 100%;
  padding: .32rem 0;
  text-align: center;
  color: #8492A6;
}
.no-data:before, .no-data:after {
    display: block;
    content: '';
    position: absolute;
    top: 50%;
    -webkit-transform: translate3d(0, -50%, 0);
            transform: translate3d(0, -50%, 0);
    width: .72rem;
    height: 1px;
    background: #99A9BF;
}
.no-data:before {
    left: 1.4rem;
}
.no-data:after {
    right: 1.4rem;
}



